import React, { Component } from "react";
import { Siteurl } from "../../services/script";
import Breadcrumb from "../common/breadcrumb";
import { connect } from "react-redux";
import CryptoJS from "crypto-js";
const liff = window.liff;
class Register extends Component {
  constructor() {
    super();
    this.state = {
      name: "",
      userLineID: "",
      pictureUrl: "",
      statusMessage: "",
      firstname: "",
      lastname: "",
      email: "",
      password: "",
      repassword: "",
      isaccept: false,
    };
  }
  componentDidMount() {
    this.setState({ isaccept: this.props.isaccept == "true" ? true : false });

    console.log("liff", liff);
    this.getProfile();
  }

  getProfile() {
    liff.init({ liffId: "1655781149-9byNMe1M" }, async () => {
      let getProfile = await liff.getProfile();
      this.setState({
        name: getProfile.displayName,
        userLineID: getProfile.userId,
        pictureUrl: getProfile.pictureUrl,
        statusMessage: getProfile.statusMessage,
        email: liff.getDecodedIDToken().email,
      });
    });
  }
  sendMessage() {
    liff
      .sendMessages([
        {
          type: "text",
          text: "Hi LIFF , " + this.state.userLineID,
        },
      ])
      .then(() => {
        liff.closeWindow();
      });
  }
  closeLIFF() {
    // 
    liff.closeWindow();
  }

  Register = () => {
    const { isaccept } = this.state;
    if (isaccept == true) {
      this.doRegister();
      return true;
    } else {
      alert("");
      return false;
    }
  };

  validateEmail(email) {
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  }

  doRegister = () => {
    const prms = {
      firstname: this.state.firstname,
      lastname: this.state.lastname,
      email: this.state.email,
      password: this.state.password,
      repassword: this.state.repassword,
      userLineID: this.state.userLineID,
      pictureUrl: this.state.userLineID,
      name: this.state.name,
    };

    if (
      prms.firstname == "" ||
      prms.lastname == "" ||
      prms.email == "" ||
      prms.password == "" ||
      prms.repassword == ""
    ) {
      alert("");
      return;
    }
    if (!this.validateEmail(prms.email)) {
      alert("");
      return;
    }

    if (prms.password != prms.repassword) {
      alert("");
      return;
    }

    prms.password = CryptoJS.MD5(prms.password).toString();
    prms.repassword = CryptoJS.MD5(prms.repassword).toString();
    fetch(Siteurl + "service/registercust", {
      method: "POST",
      headers: {
        // "X-Requested-With": "XMLHttpRequest",
      },
      body: JSON.stringify(prms),
    })
      .then((res) => res.json())
      .then(
        (result) => {
          if (result != false) {
            this.closeLIFF();
            // window.location = "/pages/login/true";
          } else {
            alert("");
            return;
          }
        },
        (error) => {
          console.log(error);
          alert(error);
          this.setState({
            isLoaded: true,
            error,
          });
        }
      );
  };

  handleChange = () => {
    this.setState({ isaccept: !this.state.isaccept });
  };

  render() {
    console.log("this.props.isaccept", this.props.isaccept);
    return (
      <div>
        <Breadcrumb title={""} />

        {/*Regsiter section*/}
        <section className="register-page section-b-space">
          <div className="container">
            <div className="row">
              <div className="col-lg-12">
                <h3></h3>
                <div className="theme-card">
                  <form className="theme-form">
                    <div className="form-row">
                      <div className="col-md-6">
                        <label htmlFor="email"></label>
                        <input
                          type="text"
                          className="form-control"
                          id="fname"
                          placeholder=""
                          value={this.state.firstname}
                          required
                          onChange={(e) =>
                            this.setState({ firstname: e.target.value })
                          }
                        />
                      </div>
                      <div className="col-md-6">
                        <label htmlFor="lname"></label>
                        <input
                          type="text"
                          className="form-control"
                          id="lname"
                          placeholder=""
                          required
                          onChange={(e) =>
                            this.setState({ lastname: e.target.value })
                          }
                        />
                      </div>
                    </div>
                    <div className="form-row">
                      <div className="col-md-6">
                        <label htmlFor="email"></label>
                        <input
                          type="email"
                          className="form-control"
                          id="email"
                          placeholder=""
                          required
                          // disabled={this.state.email != '' ? 'disabled' : ''}
                          value={this.state.email}
                          onChange={(e) =>
                            this.setState({ email: e.target.value })
                          }
                        />
                      </div>
                      <div className="col-md-6">
                        <label htmlFor="passw"></label>
                        <input
                          type="password"
                          className="form-control"
                          id="passw"
                          placeholder=""
                          required=""
                          onChange={(e) =>
                            this.setState({ password: e.target.value })
                          }
                        />
                      </div>
                      <div className="col-md-6">
                        <label htmlFor="repassw"></label>
                        <input
                          type="password"
                          className="form-control"
                          id="repassw"
                          placeholder=""
                          required=""
                          onChange={(e) =>
                            this.setState({ repassword: e.target.value })
                          }
                        />
                      </div>
                      <div className="col-md-6">
                        <span>
                          <label>
                            <input
                              type="checkbox"
                              checked={
                                this.state.isaccept == true ? "checked" : ""
                              }
                              ref="complete"
                              onChange={this.handleChange}
                            />
                            {" "}
                          </label>
                        </span>{" "}
                        <a
                          href={`${process.env.PUBLIC_URL}/pages/termandcond`}
                          className="text-center tandc"
                        >
                          
                        </a>
                      </div>
                      <a
                        href="javascript:;"
                        className="btn-petto-secondcolor  text-center"
                        onClick={() => this.Register()}
                      >
                        
                      </a>
                      {/* <a 
                        onClick={this.sendMessage.bind(this)}
                        style={{ marginRight: "20px" }}
                      >
                        Send Message
                      </a> */}
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

const mapStateToProps = (state, ownProps) => ({
  isaccept: ownProps.match.params.isaccept,
});

export default connect(mapStateToProps)(Register);
