import React, { Component } from "react";
import { connect } from "react-redux";
import Breadcrumb from "../common/breadcrumb";
import CryptoJS from "crypto-js";
import { Siteurl } from "../../services/script";
class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: "",
      isregistersuccess: false,
    };
  }
  componentDidMount() {
    this.setState({
      isregistersuccess: this.props.isregistersuccess == "true" ? true : false,
    });
  }

  Login = () => {
    const { email, password } = this.state;
    if (email == "" || password == "") {
      alert("");
      return;
    }

    let _password = CryptoJS.MD5(password).toString();
    const prms = {
      email: this.state.email,
      password: _password,
    };
    fetch(Siteurl + "service/customerlogin", {
      method: "POST",
      headers: {
        // "X-Requested-With": "XMLHttpRequest",
      },
      body: JSON.stringify(prms),
    })
      .then((res) => res.json())
      .then(
        (result) => {
          console.log("result", result);
          if (result.result != null) {
            sessionStorage.setItem("customer", JSON.stringify(result.result));
            window.location = "/";
          } else {
            alert("");
          }
        },
        (error) => {
          console.log(error);
          this.setState({
            isLoaded: true,
            error,
          });
        }
      );

    console.log(prms);
  };
  render() {
    const { isregistersuccess } = this.state;
    return (
      <div>
        <Breadcrumb title={""} />
        {/*Login section*/}
        <section className="login-page section-b-space">
          <div className="container">
            {isregistersuccess == true && (
              <center>
                <div class="alert alert-danger" role="alert">
                  {""}
                </div>
              </center>
            )}
            <div className="row">
              <div className="col-lg-6">
                <h3></h3>
                <div className="theme-card">
                  <form className="theme-form">
                    <div className="form-group">
                      <label htmlFor="email"></label>
                      <input
                        type="text"
                        className="form-control"
                        id="email"
                        placeholder=""
                        required=""
                        onChange={(e) =>
                          this.setState({ email: e.target.value })
                        }
                      />
                    </div>
                    <div className="form-group">
                      <label htmlFor="review"></label>
                      <input
                        type="password"
                        className="form-control"
                        id="review"
                        placeholder=""
                        required=""
                        onChange={(e) =>
                          this.setState({ password: e.target.value })
                        }
                      />
                    </div>
                    <a
                      href="javascript:;"
                      className="btn btn-petto"
                      onClick={() => this.Login()}
                    >
                      
                    </a>
                  </form>
                </div>
              </div>
              <div className="col-lg-6 right-login">
                <h3></h3>
                <div className="theme-card authentication-right">
                  <h6 className="title-font"></h6>
                  <p>
                     
                    Pettogo.co 
                    <br />
                    
                  </p>
                  <a
                    href={`${process.env.PUBLIC_URL}/pages/register/false`}
                    className="btn btn-petto-secondcolor"
                  >
                    
                  </a>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}
const mapStateToProps = (state, ownProps) => ({
  isregistersuccess: ownProps.match.params.isregistersuccess,
});

export default connect(mapStateToProps)(Login);
